.Main {
  min-height: 100vh;
}
.Main h1 {
  text-align: center;
  color: #bdf5ef;
  background-image: linear-gradient(
    to right,
    transparent,
    #000c045c,
    transparent
  );
  padding: 5px;
}
.Button {
  display: flex;
}
.Button button {
  margin: 10px auto;
}
@media screen and (max-width: 600px) {
  .Main h1 {
    margin-top: 30px;
  }
}
